* {
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0;
    background: #23252C;
    font-family: 'Inter', sans-serif;
}

.App {
    display: flex;
}
.container {
    width: 420px;
    margin: 0 auto;
    background: #1A1B21;
    border-radius: 10px 10px 0px 0px;
    margin-top: 2em;
}

.main-img {
    width: 100%;
    height: 350px;
    object-fit: cover;
    border-radius: 10px 10px 0px 0px;
}

.nav-bar {
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
}

.nav-bar > h1 {
    
font-family: 'Inter';
font-weight: 700;
font-size: 25px;
color: #FFFFFF;
margin: 21px 0 0 0;
}
.nav-bar > p {
font-family: 'Inter';
font-weight: 400;
font-size: 14.8px;
text-align: center;
color: #F3BF99;

}

.nav-bar > small {
font-family: 'Inter';
font-weight: 400;
font-size: 12.24px;
line-height: 150%;
color: #F5F5F5;
}
.btn {
    display: flex;
    justify-content: space-around;
    min-width: 100%;
    margin-top: 20px;
}

.btn > button {
    width: 115px;
    height: 34px;
    border: none;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 6px;
    cursor: pointer;
}

.btn button:last-child {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #5093E2;
    color: #ffffff;
}
.icons {
    margin-right: 8px;
}

/* Main-section styles  */

.main-section {
padding: 0 34px;
font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 12.24px;
line-height: 150%;
color: #DCDCDC;

}

.main-section h2 {
    color: #ffffff;
}

/*Footer */

.social-icons-ul {
    display: flex;
    justify-content: space-around;
    padding: 0 40px;
    list-style: none;
}